<script>
import dayjs from 'dayjs'
// import alipayf2f from 'alipay-ftof';
import QRCode from 'qrcodejs2'

export default {
  data() {
    return {
      swiperOption: {
        // some swiper options/callbacks
        // 所有的参数同 swiper 官方 api 参数
        // ...
        pagination:{
          el: ".pagination-home"
        }
      },
      newsCats: [],
      heroCats: [],
      videoList: [],
      alipayinfo:{
        tradeNo: "",      // 必填 商户订单主键, 就是你要生成的
        subject: "女装",      // 必填 商品概要
        totalAmount: 0.01,    // 必填 多少钱
        body: "黑丝吊带小蜡烛", // 可选 订单描述, 可以对交易或商品进行一个详细地描述，比如填写"购买商品2件共15.00元"
        timeExpress: 5       // 可选 支付超时, 默认为5分钟
      },
      isPayqrcode:false,
      qrcode:'',
      payResult: {},
      analysisurl:{
        dyurl:'',
        url:'',
        cover:'',
        music:'',
        title:'',
      },
      nodedata:{
        dyurl:'',
        url:'',
        cover:'',
        music:'',
        title:'',
      },
    }
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper
    }
  },
  filters: {
    date(val){
      return dayjs(val).format('MM/DD')
    }
  },
  created() {
    this.fetchNewsCats();
    this.fetchHeroCats();
    this.fetchVideo();
  },
  mounted() {
    console.log('alipay');
    console.log('xigua111');
    this.swiper.slideTo(3, 1000, false)
  },
  methods: {
    async fetchNewsCats(){
      const res = await this.$http.get('news/list');
      this.newsCats = res.data;
    },
    async fetchHeroCats(){
      const res = await this.$http.get('heroes/list');
      this.heroCats = res.data;
    },
    async fetchVideo(){
      const res = await this.$http.get('videos/list');
      this.videoList = res.data;
    },
    async aliPay(){
      console.log('alipay');
      const res = await this.$http.post('alipay', this.alipayinfo)
      this.payResult = res.data
      console.log(this.payResult);
      if (this.payResult.code === '10000') {
        this.qrcode = this.payResult.qr_code;
        this.payOrder()
      }
    },
    // 展示二维码
    async payOrder () {
      this.isPayqrcode = true
      // 二维码内容,一般是由后台返回的跳转链接,这里是写死的一个链接
      // this.qrcode = ''
      // 使用$nextTick确保数据渲染
      // this.$nextTick(() => {
        await this.crateQrcode()
        await this.alipayPolling()
      // })
    },
    // 生成二维码
    async crateQrcode () {
      this.qr = new QRCode('qrcode', {
        width: 150,
        height: 150, // 高度
        text: this.qrcode // 二维码内容
        // render: 'canvas' // 设置渲染方式（有两种方式 table和canvas，默认是canvas）
        // background: '#f0f'
        // foreground: '#ff0'
      })
    },
    // 开始轮询
    async alipayPolling(){
      // 二维码生成接口
      this.intervalwx = window.setInterval(()=>{
        setTimeout(this.checkPay(),0)
      },1000);
    },
    async checkPay(){
      //调取支付状态的接口，支付成功返回200
      const res = await this.$http.post('checkPay', this.alipayinfo)
      const that = this;
      if (res.data.code == 200) {
        clearInterval(that.intervalwx)
        that.intervalwx = null
      }
    },
    // 关闭弹框,清除已经生成的二维码
    closeCode () {
      this.$refs.qrcode.innerHTML = ''
    },
    handleOk(e) {
      console.log(e);
      this.isPayqrcode = false;
      this.$refs.qrcode.innerHTML = '';
    },
    handleQrcode() {
      window.open(`${this.qrcode}`)
    },

    async analysis(){
      const res = await this.$http.post('analysisurl', this.analysisurl)
      if (res.data.code) {
        this.nodedata = res.data
      } else {
        this.nodedata.dyurl = res.data
        // this.analysisurl.dyurl = res.data
      }
    },

    playAudio(){
      console.log('123');
      // var msg = new SpeechSynthesisUtterance('Help me with this code please?'); 

      // msg.pitch = 0; 

      // msg.rate = .6; 

      // window.speechSynthesis.speak(msg); 







      var msg = new SpeechSynthesisUtterance(); 

      var voices = window.speechSynthesis.getVoices(); 

      msg.voice = voices[10]; // Note: some voices don't support altering params 

      msg.voiceURI = 'native'; 

      msg.volume = 1; // 0 to 1 

      msg.rate = 1.2; // 0.1 to 10 

